<template>
  <div class="pos">
    <el-col :span="7" class="pos-or" id="or-list">
      <el-tabs>
        <el-tab-pane label="点餐">
          <el-table :data="tableData" border style="width:100%">
            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
            <el-table-column prop="count" label="量" width="50"></el-table-column>
            <el-table-column prop="price" label="金额" width="70"></el-table-column>
            <el-table-column label="操作" width="100" fixed="right">
              <template slot-scope="scope">
                <el-button type="text" sixe="small" @click="delSingleGoods(scope.row)">删除</el-button>
                <el-button type="text" sixe="small" @click="addOrderList(scope.row)">增加</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="totalDiv">
            <small>数量：</small>
            {{totalCount}} &nbsp;&nbsp;&nbsp;&nbsp;
            <small>金额：</small>
            {{totalMoney}}元
          </div>
          <div class="div-btn">
            <el-button type="warning" @click="guadan">挂单</el-button>
            <el-button type="danger" @click="delallGoods">删除</el-button>
            <el-button type="success" @click="checkout">结账</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="挂单">
          <el-table :data="tableData" border style="width:100%">
            <el-table-column prop="goodsName" label="商品名称"></el-table-column>
            <el-table-column prop="count" label="量" width="50"></el-table-column>
            <el-table-column prop="price" label="金额" width="70"></el-table-column>
            <el-table-column label="操作" width="100" fixed="right">
              <template slot-scope="scope">
                <el-button type="text" sixe="small" @click="delSingleGoods(scope.row)">删除</el-button>
                <el-button type="text" sixe="small" @click="addOrderList(scope.row)">增加</el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="totalDiv">
            <small>数量：</small>
            {{totalCount}} &nbsp;&nbsp;&nbsp;&nbsp;
            <small>金额：</small>
            {{totalMoney}}元
          </div>
          <div class="div-btn">
            <el-button type="danger" @click="delallGoods">删除</el-button>
            <el-button type="success" @click="checkout">结账</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="外卖">外卖</el-tab-pane>
      </el-tabs>
    </el-col>
    <el-col :span="17">
      <div class="often-goods">
        <div class="title">常用商品</div>
        <div class="often-goode-list">
          <ul>
            <li v-for="goods in oftenGoods" @click="addOrderList(goods)">
              <span>{{goods.goodsName}}</span>
              <span class="o-price">￥{{goods.price}}元</span>
            </li>
          </ul>
        </div>
      </div>
      <div class="goods-type">
        <el-tabs>
          <el-tab-pane label="汉堡">
            <div>
              <ul class="cookList">
                <li v-for="goods in type0Goods" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" alt />
                  </span>
                  <span>{{goods.goodsName}}</span>
                  <span>￥{{goods.price}}元</span>
                </li>
              </ul>
            </div>
          </el-tab-pane>
          <el-tab-pane label="小食">
            <div>
              <ul class="cookList">
                <li v-for="goods in type1Goods" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" alt />
                  </span>
                  <span>{{goods.goodsName}}</span>
                  <span>￥{{goods.price}}元</span>
                </li>
              </ul>
            </div>
          </el-tab-pane>
          <el-tab-pane label="饮料">
            <div>
              <ul class="cookList">
                <li v-for="goods in type2Goods" @click="addOrderList(goods) ">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" alt />
                  </span>
                  <span>{{goods.goodsName}}</span>
                  <span>￥{{goods.price}}元</span>
                </li>
              </ul>
            </div>
          </el-tab-pane>
          <el-tab-pane label="套餐">
            <div>
              <ul class="cookList">
                <li v-for="goods in type3Goods" @click="addOrderList(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" alt />
                  </span>
                  <span>{{goods.goodsName}}</span>
                  <span>￥{{goods.price}}元</span>
                </li>
              </ul>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-col>
  </div>
</template>

<script>
// import Axios from 'axios';
export default {
  name: "pos",
  data() {
    return {
      tableData: [],
      oftenGoods: [
        {
          goodsId: 1,
          goodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 2,
          goodsName: "田园鸡腿堡",
          price: 15
        },
        {
