          price: 15
        },
        {
          goodsId: 20,
          goodsName: "香脆鸡柳",
          price: 17
        }
      ],
      type0Goods: [
        {
          goodsId: 1,
          goodsImg:
            "http://5b0988e595225.cdn.sohucs.com/images/20180712/2c91b299e3dd436184493883c1e37fc3.jpeg",
          goodsName: "香辣鸡腿堡",
          price: 18
        },
        {
          goodsId: 2,
          goodsImg:
            "http://imgqn.koudaitong.com/upload_files/2015/05/05/Fs32jYb7MYPvLZA8lZ6Ujz70vLiZ.jpg%21580x580.jpg",
          goodsName: "田园鸡腿堡",
          price: 15
        },
        {
          goodsId: 3,
          goodsImg:
            "http://liaoli.kantsuu.com/UploadFiles_1650/201503/20150324213719576.jpg",
          goodsName: "和风汉堡",
          price: 15
        },
        {
          goodsId: 4,
          goodsImg: "https://i03piccdn.sogoucdn.com/85a68c38c4b3efaf",
          goodsName: "快乐全家桶",
          price: 80
        },
        {
          goodsId: 5,
          goodsImg:
            "http://img.mp.itc.cn/upload/20170108/cbcfeb3a3f904514ad5d0136d74e7d7a_th.jpg",
          goodsName: "脆皮炸鸡腿",
          price: 10
        },
        {
          goodsId: 6,
          goodsImg:
            "https://gss0.baidu.com/7Po3dSag_xI4khGko9WTAnF6hhy/zhidao/wh%3D600%2C800/sign=7e19bd833d4e251fe2a2ecfe97b6e52f/caef76094b36acaf11f0894571d98d1001e99c72.jpg",
          goodsName: "魔法鸡块",
          price: 20
        }
      ],
      type1Goods: [
        {
          goodsId: 9,
          goodsImg:
            "http://00.imgmini.eastday.com/mobile/20170916/20170916090443_a8e26ce43503d6395253af2cd0ce4aee_1.jpeg",
          goodsName: "大块鸡米花",
          price: 15
        },
        {
          goodsId: 20,
          goodsImg:
            "https://s1.st.meishij.net/rs/19/187/2609269/n2609269_144195166412286.jpg",
          goodsName: "香脆鸡柳",
          price: 17
        },
        {
          goodsId: 5,
          goodsImg:
            "http://img.mp.itc.cn/upload/20170108/cbcfeb3a3f904514ad5d0136d74e7d7a_th.jpg",
          goodsName: "脆皮炸鸡腿",
          price: 10
        },
        {
          goodsId: 6,
          goodsImg:
            "https://gss0.baidu.com/7Po3dSag_xI4khGko9WTAnF6hhy/zhidao/wh%3D600%2C800/sign=7e19bd833d4e251fe2a2ecfe97b6e52f/caef76094b36acaf11f0894571d98d1001e99c72.jpg",
          goodsName: "魔法鸡块",
          price: 20
        }
      ],
      type2Goods: [
        {
          goodsId: 7,
          goodsImg: "http://img1.gtimg.com/5/591/59151/5915177_1200x1000_0.jpg",
          goodsName: "可乐大杯",
          price: 10
        },
        {
          goodsId: 8,
          goodsImg:
            "http://5b0988e595225.cdn.sohucs.com/images/20170929/6da6785582c84ed1833e3267c0b21e76.jpeg",
          goodsName: "雪顶咖啡",
          price: 18
        }
      ],
      type3Goods: [
        {
          goodsId: 4,
          goodsImg: "https://i03piccdn.sogoucdn.com/85a68c38c4b3efaf",
          goodsName: "快乐全家桶",
          price: 80
        },
        {
          goodsId: 21,
          goodsImg:
            "https://pic.52112.com/180427/180427_222/egleNxIpgr_small.jpg",
          goodsName: "牛肉煲套餐",
          price: 77
        },
        {
          goodsId: 22,
          goodsImg: "https://i04piccdn.sogoucdn.com/a35c63bcf2297c74",
          goodsName: "鸡排堡套餐",
          price: 100
        },
        {
          goodsId: 23,
          goodsImg:
            "http://attachment2.jmw.com.cn/image/2018/11/01/f0bdbe932b871758c9993a1df6d86039.jpg",
          goodsName: "炸鸡饱套餐",
          price: 95
        }
      ],
      totalMoney: 0,
      totalCount: 0
    };
  },
  methods: {
    //添加订单列表的方法
    addOrderList(goods) {
      this.totalCount = 0; //汇总数量清0
      this.totalMoney = 0;

      //判断是否这个商品已经存在于订单列表
      let isHave = false;
      for (let i = 0; i < this.tableData.length; i++) {
        console.log(this.tableData[i].goodsId);
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true; //存在
        }
      }

      //根据isHave的值判断订单列表中是否已经有此商品
      if (isHave) {
        //存在就进行数量添加
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        arr[0].count++;
        //console.log(arr);
      } else {
        //不存在就推入数组
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
      }
      this.getAllMoney();
    },

    //删除单个商品
    delSingleGoods(goods) {
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
      this.getAllMoney();
    },
    delallGoods() {
      this.tableData = [];
      this.totalCount = 0; //汇总数量清0
      this.totalMoney = 0;
    },

    //模拟挂单
    guadan() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message: "挂单成功，非常感谢你有为店里出了一份力",
          type: "success"
        });
      } else {
        this.$message.error("不能挂空单，老板了解你急切的心情");
      }
    },
    //模拟结账
    checkout() {
      if (this.totalCount != 0) {
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
        this.$message({
          message: "结账成功，非常感谢你有为店里出了一份力",
          type: "success"
        });
      } else {
        this.$message.error("不能空结账，老板了解你急切的心情");
      }
    },

    getAllMoney() {
      this.totalCount = 0; //汇总数量清0
      this.totalMoney = 0;
      if (this.tableData) {
        //汇总数量和金额
        this.tableData.forEach(element => {
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + element.price * element.count;
        });
      }
    }
  }
  // created:function(){
  //   axios.get()
  //   .then(reponse=>{

  //   })
  //   .catch(reponse=>{

  //   })
  // },
};
</script>

<style scoped>
.pos {
  height: 100%;
}
.pos-or {
  background-color: #f9fafc;
  border-right: 1px solid #c0ccda;
  height: 100%;
  padding-left: 0.3%;
}
.div-btn {
  margin-top: 30px;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
  text-align: left;
}
.often-goode-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 10px;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  color: #5887ff;
}
.goods-type {
  clear: both;
  background-color: #f9fafc;
  padding-left: 1%;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span {
  display: block;
  float: left;
}
.foodImg {
  width: 60%;
  height: 80px;
}
.foodImg img {
  width: 100%;
  height: 100%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.totalDiv {
  background-color: #fff;
  padding: 10px;
  border-bottom: 1px solid #d3dce6;
}
</style>



